import { css } from '@emotion/react';

import { VerticalScrollableBox } from 'src/components/layout';
import {
  MagazineIntroPaper,
  MagazinePaper,
  StampPaper,
} from 'src/components/papers';
import type {
  MagazineItem,
  NormalMagazineItem,
} from 'src/hooks/useMagazineItems';
import useMagazineItems, { INTRO } from 'src/hooks/useMagazineItems';
import useProductInfo from 'src/hooks/useProductInfo';

const isNormalMagazineItem = (
  item: MagazineItem,
): item is NormalMagazineItem => {
  return item.layoutType !== INTRO;
};

// scroll-snap-points-y: repeat(100vh);
const scrollSnapTypeMandatory = css`
  overflow: scroll;
  scrollbar-width: none;
  scroll-snap-type: y mandatory;
`;

const Magazine = (): JSX.Element => {
  const { name: productName } = useProductInfo();
  const items = useMagazineItems({ productName });
  const introItem = items.find(({ layoutType }) => layoutType === INTRO);
  const normalMagazineItems = items.filter(isNormalMagazineItem);

  return (
    <VerticalScrollableBox css={scrollSnapTypeMandatory}>
      {introItem && <MagazineIntroPaper imageUrl={introItem?.imageUrl} />}
      {normalMagazineItems.map(
        ({ layoutType, imageUrl, staticContent, linkableContents }, index) => {
          const key = `${index}-${layoutType}`;
          return (
            <MagazinePaper
              key={key}
              imageUrl={imageUrl}
              layoutType={layoutType}
              staticContent={staticContent}
              linkableContents={linkableContents}
            />
          );
        },
      )}
      <StampPaper />
    </VerticalScrollableBox>
  );
};

export default Magazine;
